*
{
    margin:0;
    padding:0;
}
.home
{
    max-width:800px;
    /* align center */
    margin:0 auto;  
    margin-top:90px;
    height:100%;

}
.home__container

{
    height:70vh;
    display:flex;
    flex-direction:column;
    justify-content: center;
    align-items: center;

}
.home__formContainer {
    box-shadow:1px 4px 12px -2px rgba(0,0,0,4.3);
   background:#f6ca6c;
    padding:30px;
    border-radius:10px;
    width:500px;
}
.home__title{
    color:brown;
    font-size: 30px;
}
input{
    border:2px solid brown;
    outline:none;
    padding:15px 10px;
    width:80%;
    border-radius:8px;
    margin:10px 0;
    background:transparent;
    color:black;
    font-size:14px;
    margin-top: 10px;
}